    //////////////////// UI ////////////////////
    private clickToMainPage(index: number): void {
        if (this.homeData[index].child.length > 0) {
            this.homeData[index].isOpenChild = !this.homeData[index].isOpenChild;
        } else {
            this.$router.push(this.homeData[index].href);
        }
    }

    private clickToChildPage(index: number, childIndex: number): void {
        this.$router.push(this.homeData[index].child[childIndex].href);
    }

    private clickAngle(index: number): void {
        this.homeData[index].isOpenChild = !this.homeData[index].isOpenChild;
    }
}
Vue.component('home', Home);
